import React,{Component} from 'react';
import './App.css';
import Navi from './Components/Nav/Navi.js';
import Logo from './Components/Logo/Logo';
import Rank from './Components/Rank/Rank';
import FaceFinder from './Components/FaceFinder/FaceFinder';
import Particles from 'react-particles-js';
import ImgForm from './Components/ImgForm/ImgForm';
import Clarifai from 'clarifai';


const app = new Clarifai.App({
    apiKey: '819644da0fbf4b548d16b072c9b6ae77'
   });

const particleoptions = {
  
 
    "particles": {
        "number": {
            "value": 2000,
            "density": {
                "enable": false
            }
        },
        "size": {
            "value": 3,
            "random": true,
            "anim": {
                "speed": 4,
                "size_min": 0.3
            }
        },
        "line_linked": {
            "enable": false
        },
        "move": {
            "random": true,
            "speed": 1,
            "direction": "top",
            "out_mode": "out"
        }
    },
    "interactivity": {
        "events": {
            "onhover": {
                "enable": false,
                "mode": "bubble"
            },
            "onclick": {
                "enable": !true,
                "mode": "repulse"
            }
        },
        "modes": {
            "bubble": {
                "distance": 250,
                "duration": 2,
                "size": 0,
                "opacity": 0
            },
            "repulse": {
                "distance": 400,
                "duration": 4
            }
        }
      }}

class App extends Component{
    constructor(){
        super();
        this.state={
            input:'',
            imgUrl:'',
            box: {}
              
        }
    }

calFaceLocation = (data)=>{
    const clarifaiFace= data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputimage')
    const width = Number(image.width);
    const height = Number(image.height);
    console.log(width,height);
    return{
        leftCol: clarifaiFace.left_col*width,
        topRow: clarifaiFace.top_row*height,
        rightCol: width-(clarifaiFace.right_col*width),
        bottomRow: height-(clarifaiFace.bottom_row*height)
    }
    
}

displayFaceBox =(box)=>{
    console.log(box);
    this.setState({box:box});
}

  onInputChange = (event) => {
      this.setState({input: event.target.value});
  }

  onButtonClick =() =>{
    this.setState({imgUrl : this.state.input});
      console.log('Clicked');
      app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.input)
      .then(response =>this.displayFaceBox( this.calFaceLocation(response)))
      .catch(err=>console.log(err));
      
  }


  render(){
  return (
    <div className="App">
            <Particles className='particles' 
              params={particleoptions}
               
              />
    
    <Logo />
    
    <ImgForm onInputChange={this.onInputChange} onButtonClick={this.onButtonClick} />
    <FaceFinder box= {this.state.box} imgUrl={this.state.imgUrl}/>
    </div>
  );
}}
export default App;
